.Header{
  width: 100%;
  height: 60px;
  position: fixed;
  top: 0;
  z-index: 1000;
  background-color: #302b40;
  border-bottom: 1px solid rgba(60, 50, 84, 0.5);
}
.header__wrapper{
  width: 98%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  
}
.header__logo{
  display: flex;
  align-items: center;
  height: 60px;
  letter-spacing: 10px;
  padding-left: 7%;
  font-size: 35px;
  width: 30%;
}
.header__login{
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 10px;
  width: 30%;
  letter-spacing: 3px;
  text-decoration: none;
}
.header__login:hover{
  color:#ccc;
}
.header_login__b{
  border:none;
  background: none;
  font-size: 13px;
  color: rgb(201, 190, 141);
  outline: none;
  text-transform: uppercase;
}
.burger{
  display: flex;
  position: relative;
  align-items: center;
  width: 70%;
  height: 60px;
  outline: none;
}
.nav-toggle{
  display: none;
  width: 25px;
  padding: 10px 0;

  position: absolute;
  top:20px;
  right:15px;
  z-index: 1;

  cursor: pointer;
}
.nav-toggle:focus{
  outline: 0;
}
.nav-toggle-item{
  display: block;
  width: 100%;
  height: 3px;

  background-color:#fff;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
}
.nav-toggle-item:before,
.nav-toggle-item:after{
  content: '';
  width: 100%;
  height: 3px;

  background-color: #fff;

  position: absolute;
  left: 0;
  z-index: 1;
  transition: .2s linear
}
.nav-toggle-item:before{
  top: -8px;
}
.nav-toggle-item:after{
  bottom: -8px;
}
.nav-toggle.active .nav-toggle-item{
  background:  none;
}
.nav-toggle.active .nav-toggle-item:before{
  top:0;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.nav-toggle.active .nav-toggle-item:after{
  bottom:0;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}


